/* Section catalog list start */
.section-catalog-list {
}
.section-catalog-list .section__title {
  margin-bottom: 20px;
}
.section-catalog-list__category {
  margin-bottom: 40px;
}
@media (min-width: 576px) {
  .section-catalog-list__category {
    margin-bottom: 60px;
  }
}
@media (min-width: 768px) {
  .section-catalog-list__category {
    margin-bottom: 80px;
  }
}
.section-catalog-list__category__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin: -5px -15px;
}
.section-catalog-list__category__item {
  color: var(--text-gray-ligth);
  text-decoration: none;
  transition: color 0.25s;
  text-transform: lowercase;
  font-size: 1em;
  padding: 5px 10px;
  margin: 0 5px;
}
@media (min-width: 576px) {
  .section-catalog-list__category__item {
    font-size: 1.25em;
    padding: 5px 15px;
    margin: 5px 10px;
  }
  .section-catalog-list__category__wrapper {
    margin: -10px -25px;
  }
}
@media (min-width: 768px) {
  .section-catalog-list__category__item {
    font-size: 1.5em;
    margin: 5px 15px;
  }
  .section-catalog-list__category__wrapper {
    margin: -10px -30px;
  }
}
@media (min-width: 992px) {
  .section-catalog-list__category__item {
    font-size: 1.875em;
    margin: 5px 20px;
  }
  .section-catalog-list__category__wrapper {
    margin: -10px -35px;
  }
}
.section-catalog-list__category__item.section-catalog-list__category__item--active {
  color: var(--text-accent);
}
.section-catalog-list__category__item--placeholder:before {
  content: "";
  display: block;
  height: calc(var(--line-height-standart) * 1em);
  background: var(--background-placeholder);
  animation: var(--placeholder-animation);
}
.section-catalog-list__category__item--placeholder:nth-child(3n + 1):before {
  width: 7em;
}
.section-catalog-list__category__item--placeholder:nth-child(3n + 2):before {
  width: 2em;
}
.section-catalog-list__category__item--placeholder:nth-child(3n + 3):before {
  width: 5em;
}
.section-catalog-list__category__item:hover {
  color: var(--text);
}
.section-catalog-list__list {
}
.section-catalog-list__list__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin: -7px;
}
.section-catalog-list__list__item {
  flex: 0 0;
  flex-basis: calc(50% - 14px);
  margin: 7px;
}

@media (min-width: 576px) {
  .section-catalog-list__list__wrapper {
    margin: -15px;
  }
  .section-catalog-list__list__item {
    flex-basis: calc(50% - 30px);
    margin: 15px;
  }
}

@media (min-width: 768px) {
  .section-catalog-list__list__item {
    flex-basis: calc(33.333% - 30px);
  }
}

.section-catalog-list__list__notice {
  text-align: center;
  flex: 1 1 100%;
  margin: 15px;
}
/* Section catalog list end */
