.footer {
  background: var(--background-accent);
  color: var(--text-reverse);
  margin-top: 50px;
  padding-bottom: 60px;
}
@media (min-width: 992px) {
  .footer {
    margin-bottom: 0;
  }
}
.footer img,
.footer svg {
  display: block;
}

.footer__container {
  padding-top: 30px;
  padding-bottom: 30px;
}

.footer__row-1 {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-size: 0.875em;
  margin-bottom: 40px;
}
.footer__row-1__logo {
  width: 150px;
  flex: 0 0 auto;
}
@media (min-width: 576px) {
  .footer__row-1__logo {
    width: 200px;
  }
  .footer__container {
    padding-top: 55px;
    padding-bottom: 45px;
  }
  .footer__row-1 {
    align-items: center;
    font-size: 1.25em;
    margin-bottom: 100px;
  }
}

.footer__row-1__logo img {
  width: 100%;
  height: auto;
}
.footer__row-1__list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 20px;
  display: flex;
  flex-direction: column;
  text-align: right;
}

@media (min-width: 576px) {
  .footer__row-1__list {
    flex-direction: row;
    justify-content: flex-end;
    flex-wrap: wrap;
  }
}

@media (min-width: 576px) {
  .footer__row-1__item {
    margin-left: 50px;
  }
}
.footer__row-1__item a {
  color: inherit;
  text-decoration: none;
  display: block;
  padding-top: 5px;
  padding-bottom: 5px;
}

.footer__row-2 {
  font-size: 0.875em;
  margin-bottom: 40px;
}
@media (min-width: 576px) {
  .footer__row-2 {
    font-size: 1.25em;
    margin-bottom: 100px;
  }
}
.footer__row-2 p {
  margin: 0;
}
.footer__row-2 p a {
  color: inherit;
  text-decoration: none;
}
.footer__row-2__list {
  list-style: none;
  margin-top: 15px;
  margin-bottom: 50px;
  padding-left: 0;
  display: flex;
}
.footer__row-2__item {
  margin-right: 18px;
}
.footer__row-2__item a {
  display: block;
  color: inherit;
  text-decoration: none;
}

.footer__row-3 {
  display: flex;
  flex-direction: column;
  font-size: 0.75em;
}
.footer__row-3 a {
  color: inherit;
}
.footer__row-3 p {
  margin: 0;
  margin-bottom: 10px;
}
.footer__row-3 p:nth-child(2) {
  order: 1;
  margin-bottom: 0;
  margin-top: 30px;
}

@media (min-width: 768px) {
  .footer__row-3 p {
    margin-bottom: 0;
  }
  .footer__row-3 p:nth-child(2) {
    order: 0;
    margin-top: 0;
  }
  .footer__row-3 {
    font-size: 1em;
    flex-direction: row;
    justify-content: space-between;
  }
}
