/* Section about start */
.section-about {
}
.section-about .container {
  display: flex;
  flex-direction: column;
}
.section-about__col-left {
}
.section-about__col-right {
  display: flex;
  justify-content: center;
  order: -1;
}
@media (min-width: 992px) {
  .section-about__col-left {
    flex: 0 0;
    flex-basis: calc(50% - 15px);
    padding-right: 15px;
  }
  .section-about__col-right {
    flex: 0 0;
    flex-basis: calc(50% + 15px);
    order: -1;
  }
  .section-about .container {
    flex-direction: row;
  }
  .section-about__col-right {
    order: 0;
  }
}
.section-about__col-right img {
  max-width: 100%;
}
.section-about .container .section__title {
  display: none;
}
.section-about .section__title {
  color: var(--text-accent);
  margin-bottom: 0;
}
.section-about p {
  font-size: 1em;
  margin: 1.25em 0 0;
}
.section-about p strong {
  color: var(--text-accent);
}
@media (min-width: 768px) {
  .section-about p {
    font-size: 1.25em;
  }
}
@media (min-width: 992px) {
  .section-about > .section__title {
    display: none;
  }
  .section-about .container .section__title {
    display: block;
  }
  .section-about p {
    font-size: 1.5em;
  }
}

/* Section about end */
