.basket-icon {
    border: 0;
    background: none;
    padding: 0;
    border-radius: 0;
    display: flex;   
    align-items: flex-start;
    cursor: pointer;
}

.basket-icon__icon {
}
.basket-icon__icon svg {
    display: block;
}

.basket-icon__circle {
    background: var(--background-accent-sup);
    color: var(--text-reverse);
    font-family: var(--font-sup);
    font-weight: 700;
    font-size: 16px;
    line-height: 1;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    margin-left: 5px;
}