.carousel__slider-tray {
  display: flex;
  align-items: flex-start;
}
.carousel.container .carousel__slider {
  margin-left: -8px;
  margin-right: -8px;
}

.carousel.container .carousel__slide {
  margin-left: 8px;
  margin-right: 8px;
}

@media (min-width: 768px) {
  .carousel.container .carousel__slider {
    margin-left: -15px;
    margin-right: -15px;
  }
  .carousel.container .carousel__slide {
    margin-left: 15px;
    margin-right: 15px;
  }
}

.carousel__controls {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  margin-top: 25px;
}

@media (min-width: 768px) {
  .carousel__controls {
    margin-top: 50px;
  }
}

.carousel__controls .carousel__dot-group {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: -5px;
}
.carousel__controls .carousel__dot {
  background: transparent;
  border: 0;
  flex: 0 0 auto;
  padding: 5px 5px;
}
.carousel__controls .carousel__dot span {
  display: block;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 3px solid var(--background-accent);
  transition: background-color 0.25s;
}
.carousel__controls .carousel__dot--selected span {
  background-color: var(--background-accent);
}
.carousel__controls .carousel__back-button,
.carousel__controls .carousel__next-button {
  background: none;
  border: 0;
  padding: 5px;
  flex: 0 0 auto;
}
.carousel__controls .carousel__back-button {
  margin-right: 5px;
}
.carousel__controls .carousel__next-button {
  margin-left: 5px;
}
.carousel__controls .carousel__back-button svg,
.carousel__controls .carousel__next-button svg {
  display: block;
}

.carousel__thumbs--horizontal {
  position: relative;
  overflow: hidden;
  touch-action: pan-y pinch-zoom;
}
.carousel__thumbs--vertical {
  position: relative;
  overflow: hidden;
}
.carousel__thumbs__tray {
  display: flex;
  align-items: flex-start;
  transition: transform 0.5s;
  transition-timing-function: ease;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  will-change: transform;
}
.carousel__thumbs__slide {
  border: 0;
  padding: 0;
  background: none;
  border-radius: 0;
}
.carousel__thumbs__slide--vertical {
  position: relative;
}
.carousel__thumbs__slide--vertical .carousel__thumbs__slide-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
