/* Section story start */
.section-stories {
}
.section-stories__story {
}
.section-stories__story__img {
  position: relative;
  background: var(--background-placeholder);
  border: 3px solid var(--background-accent);
  overflow: hidden;
  border-radius: 50%;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .section-stories__story__img {
    border-width: 5px;
  }
}
.section-stories__story__img:before {
  content: "";
  padding-top: 100%;
  width: 0;
  display: block;
}
.section-stories__story__img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.section-stories__story__title {
  font-size: 0.75em;
  min-height: calc(var(--line-height-standart) * 1em);
  text-align: center;
}
@media (min-width: 576px) {
  .section-stories__story__title {
    font-size: 1em;
  }
}

@media (min-width: 992px) {
  .section-stories__story__title {
    font-size: 1.25em;
  }
}
.section-stories__story--placeholder .section-stories__story__title {
  background: var(--background-placeholder);
  animation: var(--placeholder-animation);
}
.section-stories__story--placeholder .section-stories__story__img {
  animation: var(--placeholder-animation-background-color);
}

/* Section story end */
