/* Section run line start */
.section-run-line {
  background: var(--background-accent);
  color: var(--text-reverse);
  font-size: 30px;
  padding-top: 0.7em;
  padding-bottom: 0.7em;
  overflow: hidden;
}
.section-run-line__line {
  display: flex;
  /* justify-content: center; */
  text-align: center;
  text-transform: uppercase;
  font-family: var(--font-sup);
  font-weight: 700;
}
.section-run-line__line__wrapper {
  display: flex;
  will-change: transform;
  animation: run-line-move 20s linear infinite;
}
.section-run-line__line__item {
  flex: 0 0 auto;
  white-space: nowrap;
  padding-right: 1.7em;
}
@keyframes run-line-move {
  from {
    transform: translateX(0);
  }
  to {
    transform: translate(-100%);
  }
}
/* Section run line end */
