:root {
  --background: #f3f3f3;
  --background-accent: #5c5681;
  --background-accent-sup: #4cd702;
  --background-accent-sup-1: #fbc624;
  --background-reverse: #0d0d0d;
  --background-placeholder: #dbd7d2;
  --background-placehodler-animation: rgba(219, 215, 210, 0.5);
  --text: #0d0d0d;
  --text-accent: #5c5681;
  --text-accent-sup: #4cd702;
  --text-gray-ligth: #bab9b9;
  --text-gray: #868686;
  --text-gray-dark: #474747;
  --text-reverse: #ffffff;
  --font: "Namu", Verdana, Arial, sans-serif;
  --font-accent: "Montserrat Alternates", Arial, sans-serif;
  --font-sup: "Handjet", sans-serif;
  --line-height-standart: 1.1;
  --placeholder-animation: placeholder-glow 2s ease-in-out infinite;
  --placeholder-animation-background-color: placeholder-background-color 2s
    ease-in-out infinite;
}
