.section-padding {
  padding-top: 50px;
  padding-bottom: 50px;
}

.section__title {
  font-family: var(--font-accent);
  font-weight: 900;
  text-transform: uppercase;
  font-size: 50px;
  margin-top: 0;
  margin-bottom: 25px;
}
@media (min-width: 576px) {
  .section__title {
    font-size: 60px;
    margin-bottom: 50px;
  }
}
@media (min-width: 768px) {
  .section__title {
    font-size: 80px;
  }
}
@media (min-width: 992px) {
  .section__title {
    font-size: 100px;
  }
}
@media (min-width: 1200px) {
  .section__title {
    font-size: 120px;
  }
}
.section__title.section__title--small {
  font-size: 40px;
}

.section--fixed-header {
  margin-top: -110px;
  padding-top: 110px;
}

@import url(./Banner.css);
@import url(./RunLine.css);
@import url(./Stories.css);
@import url(./Categories.css);
@import url(./About.css);
@import url(./Socials.css);
@import url(./ListProducts.css);
@import url(./ProductInfo.css);
@import url(./Recommended.css);
