/* Section socials end */
.section-socials__list {
  border: 5px solid #fbc624;
  border-radius: 20px;
  padding: 40px 15px;
  display: flex;
  flex-direction: column;
}

.section-socials__left {
  padding-left: 15px;
  padding-right: 15px;
  font-size: 18px;
  margin-bottom: 30px;
}

.section-socials__right {
  padding-left: 15px;
  padding-right: 15px;
}

.section-socials__right input {
  font-size: 16px;
  line-height: auto;
  height: 2.5em;
  padding: 0 1em;
  border: 0px;
  border-radius: 5px;
  margin-bottom: 1em;
  width: 100%;
  background: #e5e5e5;
  color: #0d0d0d;
  box-shadow: inset 0 0 0 1px transparent;
  transition: box-shadow 0.2s, background-color 0.2s;
}
.section-socials__right input:placeholder {
  color: #838383;
}

.section-socials__right input:focus {
  outline: none;
  box-shadow: inset 0 0 0 1px #4cd702;
  background: #fff;
}

.section-socials__right button {
  cursor: pointer;
  padding: 0.6em 1em;
  border: 0;
  background: var(--background-accent-sup);
  font-size: 16px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

@media (min-width: 576px) {
  .section-socials__right button {
    width: auto;
  }
  .section-socials__left {
    flex: 0 0 50%;
  }
  .section-socials__right {
    flex: 0 0 50%;
  }
  .section-socials__left {
    font-size: 22px;
    margin-bottom: 0;
  }
  .section-socials__list {
    align-items: center;
    flex-direction: row;
  }
}

@media (min-width: 768px) {
  .section-socials__list {
    padding: 40px 50px;
  }
  .section-socials__left {
    font-size: 26px;
  }
}

@media (min-width: 992px) {
  .section-socials__list {
    padding: 60px 75px;
  }
  .section-socials__left {
    font-size: 32px;
  }
  .section-socials__right input {
    font-size: 20px;
  }
}

.section-socials__right button svg {
  margin-left: 10px;
}
