/* Section banner start */
.section-banner {
  position: relative;
  z-index: 0;
}
.section-banner__decor {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.section-banner__content {
  position: relative;
  margin-top: 30px;
  padding-bottom: 40px;
  display: flex;
  align-items: flex-end;
  color: inherit;
  text-decoration: none;
}
.section-banner__content:before {
  content: "";
  width: 0;
  display: block;
  padding-top: 75%;
}
@media (min-width: 576px) {
  .section-banner__content:before {
    padding-top: 50%;
  }
}
/* .section-banner__content__decor {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
} */
.section-banner__content__title {
  position: relative;
  max-width: 7em;
  font-size: 40px;
  text-transform: uppercase;
  font-family: var(--font-accent);
  color: var(--text-reverse);
  /* transform: translate(-50%, 0); */
}

@media (min-width: 576px) {
  .section-banner__content__title {
    font-size: 50px;
  }
}

@media (min-width: 768px) {
  .section-banner__content__title {
    font-size: 60px;
  }
}
@media (min-width: 992px) {
  .section-banner__content__title {
    font-size: 70px;
  }
}
.section-banner__content__title svg {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(35%, -35%);
  width: 1.37em;
  height: 1.37em;
}
.section-banner__content__title svg circle {
  fill: #fbc624;
}
.section-banner__content__title svg path {
  fill: #000;
}
/* Section banner end */
