.header {
  position: relative;
  z-index: 1;
}

.header-reverse {
  color: var(--text-reverse);
}

@media (min-width: 992px) {
  .header-reverse .header__actions {
    color: var(--text-reverse);
  }
}

.header__container {
  display: flex;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 25px;
  height: 100px;
}

.header__logo {
  margin-right: auto;
}
@media (min-width: 992px) {
  .header__logo {
    margin-right: 30px;
  }
}

.header__logo img {
  display: block;
  height: 60px;
  width: auto;
}

ul.header__links {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: auto;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translate(100%, 0);
}

@media (min-width: 992px) {
  ul.header__links {
    position: static;
    transform: translate(0, 0);
  }
}

li.header__link {
}

li.header__link a {
  display: block;
  white-space: nowrap;
  color: inherit;
  text-decoration: none;
  line-height: 1;
  padding: 0.5em 1em;
  font-size: 20px;
  text-transform: lowercase;
}

.header__basket {
  color: inherit;
}

.header__burger {
  background: none;
  padding: 0;
  border-radius: 0;
  border: 0;
  color: inherit;
}

.header__actions {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  color: var(--text-accent);
  background: #ececec;
  border-radius: 20px 20px 0 0;
  border: 2px solid #4cd702;
  border-bottom: 0px;
  padding: 20px;
}

.header__actions__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 15px;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.header__actions button {
  border: 0;
  border-radius: 0;
  background: none;
  color: inherit;
  padding: 0;
  cursor: pointer;
}

.header__actions button svg {
  display: block;
}

@media (min-width: 992px) {
  .header__actions {
    position: static;
    width: auto;
    background: none;
    color: inherit;
    border-radius: 0;
    border: 0;
    margin-left: 80px;
  }
  .header__t-short,
  .header__favorite,
  .header__burger {
    display: none;
  }
  .header__user {
    order: -1;
  }
}

.fixed-buttons {
  display: flex;
  flex-direction: column;
  gap: 8px;
  position: fixed;
  bottom: 80px;
  right: 14px;
}

.fixed-buttons button {
  background: none;
  cursor: pointer;
  border: 0;
  border-radius: 0;
  padding: 0;
  opacity: 0;
  transform: translate(0, 50px);
  pointer-events: none;
  transition: opacity 0.2s, transform 0.2s;
}

.fixed-buttons svg {
  width: 55px;
  height: 55px;
  display: block;
}

@media (min-width: 768px) {
  .fixed-buttons svg {
    width: 96px;
    height: 96px;
  }
  .fixed-buttons {
    gap: 15px;
  }
}

@media (min-width: 992px) {
  .fixed-buttons {
    bottom: 15px;
  }
}

.wats-app-widget.wats-app-widget--show,
.scroll-button.scroll-button--show {
  pointer-events: auto;
  opacity: 1;
  transform: translate(0, 0);
}

.wats-app-widget svg {
  display: block;
}
