.button-scroll-to-top {
    position: fixed;
    z-index: 100;
    bottom: 48px;
    right: 48px;
    width: 96px;
    height: 96px;
    background: none;
    padding: 0;
    border: 0;
    transition: opacity 0.25s, transform 0.25s ease-in-out;
    opacity: 0;
    transform: translate(0, 50px);
    cursor: pointer;
    pointer-events: none;
}
.button-scroll-to-top--show {
    opacity: 1;
    transform: translate(0, 0);
    pointer-events: auto;
}
.button-scroll-to-top svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}