.product-cart-vertical {
  display: block;
  color: inherit;
  text-decoration: none;
  position: relative;
}
.product-cart-vertical--sale:before {
  content: "sale";
  text-transform: uppercase;
  font-family: var(--font-sup);
  font-size: 16px;
  font-weight: 700;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: var(--background-accent);
  color: var(--text-reverse);
  border-radius: 50%;
  width: 2.8333em;
  height: 2.8333em;
  position: absolute;
  pointer-events: none;
  z-index: 1;
  top: 10px;
  right: 18px;
}
@media (min-width: 576px) {
  .product-cart-vertical--sale:before {
    font-size: 30px;
  }
}
.product-cart-vertical__img {
  position: relative;
  overflow: hidden;
  border-radius: 20px;
  margin-bottom: 10px;
  background: var(--background-placeholder);
}
.product-cart-vertical__img:before {
  content: "";
  display: block;
  width: 0;
  padding-top: 146%;
}
.product-cart-vertical__img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.product-cart-vertical__name {
  min-height: calc(var(--line-height-standart) * 1em);
  margin-bottom: 5px;
  font-size: 0.875em;
}
@media (min-width: 576px) {
  .product-cart-vertical__name {
    font-size: 1em;
  }
}
.product-cart-vertical--placeholder .product-cart-vertical__name {
  background: var(--background-placeholder);
  max-width: 15em;
  animation: var(--placeholder-animation);
}
.product-cart-vertical--placeholder .product-cart-vertical__img {
  animation: var(--placeholder-animation-background-color);
}
.product-cart-vertical__price {
  display: flex;
  flex-wrap: wrap;
  color: var(--text-gray);
}
.product-cart-vertical__price__old {
  text-decoration: line-through;
  margin-right: 0.625em;
}
.product-cart-vertical__price__current {
  min-height: calc(var(--line-height-standart) * 1em);
}
.product-cart-vertical--placeholder .product-cart-vertical__price__current {
  background: var(--background-placeholder);
  width: 100%;
  max-width: 8em;
  animation: var(--placeholder-animation);
}

.product-cart-vertical--favorite.product-cart-vertical--sale:before {
  right: auto;
  left: 18px;
}

.product-cart-vertical--favorite svg {
  position: absolute;
  top: 18px;
  right: 18px;
  opacity: 1;
  transition: opacity 0.2s;
}

.product-cart-vertical--favorite:hover svg {
  opacity: 1;
}

@media (min-width: 768px) {
  .product-cart-vertical--favorite svg {
    width: 36px;
    height: 33px;
    opacity: 0;
  }
}
