* {
  box-sizing: border-box;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type="number"] {
  -moz-appearance: textfield; /* Firefox */
}


/* Globals styles */

html, body, #root {
  min-height: 100vh;
}
#root {
  display: flex;
  flex-direction: column;
}
#root > main {
  flex-grow: 1;
}

body {
  background: var(--background);
  color: var(--text);
  font-family: var(--font);
  font-weight: 300;
  font-size: 1rem;
  line-height: var(--line-height-standart);
  overflow-x: hidden;
}
button {
  font-weight: inherit;
}

.container {
  max-width: 1230px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

@keyframes placeholder-glow {
  50% {
    opacity: .4;
  }
}

@keyframes placeholder-background-color {
  50% {
    background-color: var(--background-placehodler-animation);
  }
}